:root{
  --main-color: darkblue;
  --secondary-color: yellow;
}

#root, body, html{
  height: 100%;
  margin: 0;
  padding:0;
}

.App {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.outlet-container{
  display: flex;

}

.page-container{
  width: 100%;
  height: 100%;
}


.cursor-pointer{
  cursor: pointer;
}
