.product-list{
    list-style: none;
    display: flex;
    flex-direction: row;
}

.product-list-item{
    padding:4px ;
    border: solid 1px var(--main-color);
    border-radius: 5px;
    height: 300px;
    width: 300px;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    margin: 3px;
}

.product-list-item img{
    width: 250px;
    border-radius: 5px;
}